			      	<!--  select v-model  optionvalue -->
			      	<select >
			      		<option  value = '' >
			      			   			
			      		</option>
			      	</select>
			      </template>
			    </el-table-column>
			    <el-table-column
			      prop="address"
			      label=""
			      show-overflow-tooltip>
			      <template slot-scope='scope'>
			      	255
			      </template>
			    </el-table-column>
			  </el-table>
		</div>
	</div>
</template>

<script>
//  element-uiCart.vue
export default {

  name: 'CourseBuy',

  data() {
    return {
    	shopCartList:[]
    };
  },
  methods:{
  	handleSelectionChange(val) {
		console.log(val);
		this.multipleSelection = val;
	}
  }
};
</script>

<style lang="css" scoped>
